import React from 'react';
import { Query, renderToStringWithData } from 'react-apollo';
import { gql } from 'apollo-boost';

const getAuthorsQuery = gql`
  {
    authors {
      name
      id
    }
  }
`

const UsersList = () => (
    <Query query={getAuthorsQuery}>
        {({ loading, error, data }) => {
            data.authors
        }}
    </Query>
);

export default UsersList;